#Mainimg{
    background: url(./logout.png);
    width: 40px;
    height: 40px;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;

}

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.__1body{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100%;

}
._body{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* background: red; */
    min-height: 100%;
    /* border: 1px solid black; */
}

.container{
    width: 95%;
    height: 95%;
    /* border: 1px solid red; */
    display: flex;
    /* justify-content: center; */
    align-items: center;
    flex-direction: column;
}
.upper{
    width: 95%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 7px;
    height: 100px;
    /* border-bottom: 1px solid red; */
}
#h{
    font-family: Arial;
    font-size: 32px;
    /* border: 1px solid orange; */
    margin-top: -10px;
}
.user{
    width: 200px;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    /* border: 1px solid orange; */
}
._Image{
    background: url(./user.jpeg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    width: 80px;
    margin-left: -10px;
    height: 80px;
    /* border: 1px solid black; */
}
#hus{
    font-size: 22px;
    /* border: 1px solid red; */
    color:black;
    margin-left: 5px;
}
.down{
    width: 95%;
    height: 85%;
    /* border: 1px solid black; */
    box-shadow: 0 11px 22px 5px rgba(0,0,0,.5);
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    row-gap: 5px;
    column-gap: 5px;
    flex-wrap: wrap;
    color:white;
    overflow-y: auto;
}
#main{
    min-width:300px;
    max-width:300px;
    min-height: 200px;
    color:black;
    display: flex;
    /* justify-content: center; */
    align-items: center;
    flex-direction: column;
    /* border: 1px solid black; */
    box-shadow: 0 7px 15px 1px black;
    margin: 5px;
     text-decoration: none;
    border-radius: 10px;
}
ul,li{
    list-style: none;
    text-decoration: none;
}
