*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.body_1{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    background: yellow;
    flex-direction: column;
    height: 100vh;
}
#lastline{
  margin-top: 20px;
  font-size: 24px;
  color:blue;
}
#pp{
  margin-top: 2%;
  font-size: 22px;
  color:black;
  border-bottom: 2px solid black;
}
.Playboard{
  /* border: 1px solid black; */
  box-shadow: 0 11px 21px 5px rgb(84, 82, 82);
  border-radius: 20px;
  width: 70%;
  height: 600px;
  margin-top: 5%;
  padding: 10px;
  
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.players{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-left: 2%;
  margin-right: 4%;
}
.main_play{
  width: 65%;
  height: 75%;
}
#one{
  background: white;
  color:black;
  
}
.same{
  width: 120px;
  height: 120px;
  display:flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 12px 22px 2px rgba(0,0,0,.2);
}
td,tr{
  /* border: 1px solid black; */
  width: 40px;
  gap: 10px;
  text-align: center;
  font-size: 32px;
  color:aqua;
  text-shadow: 2px 2px 4px black;
  height: 40px;
}
td{
  /* border: 1px solid black; */
  box-shadow: 2px 2px 4px black;
}
table{
  /* border: 1px solid red; */
  border-top: 1px solid rgba(0,0,0,.2);
  border-left: 1px solid rgba(0,0,0,.2);

}
#ph{
  font-family:monospace;
  font-size: 24px;
  /* border: 1px solid black; */
  margin-top: 0%;
}
span{
  font-family:'Courier New', Courier;
  font-size: 22px;
  color:red;
  word-spacing: 2px ;
  text-shadow: 2px 2px 4px black;
}

@media (max-width:500px) {
    #ph{
        margin-top: -0%;
        color:green;
    }
    .Playboard{
        margin-top: 10%;
        width: 90%;
    }
    #pp{
      color:black;
      font-size: 22px;
      margin-top: -30%;
      border-bottom: 2px solid black;
    }
    td,td{
        min-width: 28px;
        height: 28px;
    }
    #same{
        width: 70px;
        height: 70px;

    }
    .players{
        margin-left: 10%;
        margin-right: 10%;
    }
}