*{
  margin: 0;
  padding:0;
  box-sizing: border-box;
}
._body
{
  display: flex;
  justify-content: center;
  align-items: center;
  /* border: 1px solid black; */
  width: 100%;
  height: 100vh;

}p{
    text-align: center;
}
.con{
  min-width: 95%;
  height: 98%;
  display: flex;
   justify-content: center;
    align-items: center;
    flex-direction: column;
    /* border: 1px solid black; */
}
.sp{
    font-size: 52px;
    color:black;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    margin-top: 1%;
    margin-left: -90%;
    /* border: 1px solid red;  */
} 
.headings{
    /* border: 1px solid red; */
    width: 700px;
    height: 200px;
    display: flex;
    padding: 10px;
    margin-top: 1%;
    margin-left: 10%;
    justify-content: center;
    align-items: left;
    flex-direction: column;

  }
   #llog{
    background: orange;
  }
#signh2
  {
    font-size: 42px;
    text-align: left;
    margin-top: 5%;
    width: 95%;
    color:white;
    text-shadow: 2px 2px 4px red;
    /* border: 1px solid yellow; */
  }
  #signh3
  {
    font-size: 50px;
    font-weight: 800;
    text-align: left;
    word-spacing: 2px;
    width: 95%;color:white;
    text-shadow: 2px 2px 4px red;
    /* border: 1px solid orange; */
  }
  .form{
    /* border: 1px solid red; */
    border-top: 1px solid rgba(0,0,0,.1);
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    box-shadow: 0 21px 32px -10px rgba(0,0,0,.5);
    flex-direction: column;
    width: 90%;
    height: 500px;
    margin-top: -20%;
  }
   .inputfeild{
    /* border: 1px solid orange; */
    width: 90%;
    padding: 5px;
    height: 100px;
    margin-top: 5px;
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    margin-bottom: 10px;
  }

  #lbl{
    margin-top: -1%;
    margin-bottom: 5px;
    /* font-weight: 1000; */
    font-size: 20px;
    font-family: monospace;
  }
  #inp{
    /* margin-top: 5%; */
    width: 95%;
    text-align: left;
    padding: 5px;
    height: 60px;
    border: 1px solid rgba(0,0,0,0.2);
    outline: none;
    border-radius: 10px;
  }
 .last{
    /* border: 1px solid pink; */
    width: 95%;
    margin-top: 40px;
    margin-left: 50px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-content: center;
    height: 80px;
  }

/* .buttons_l{
    width: 90%;
    
    height: 60px;
  } */



  #ll{
    background:orange;
  }

   #diff{
    background:rgb(75, 246, 75);
    color:white;
    height: auto;
   /* padding: 5px; */
  }
@media (max-width:500px) {
  .headings{
    /* border: 1px solid red; */
    min-height: 100px;
    margin-top: 10%;
    margin-bottom: 30%;
    width: 90%;
    display: flex;
    padding: 5px;
    margin-left: 0%;
    justify-content: center;
    align-items: left;
    flex-direction: column;
  }
  #signh2
  {
    font-size: 22px;
    text-align: left;
    margin-top: 5%;
    width: 95%;
    color:white;
    text-shadow: 2px 2px 4px red;
    /* border: 1px solid yellow; */
  }
  #signh3
  {
    font-size: 22px;
    font-weight: 800;
    text-align: left;
    word-spacing: 2px;
    width: 95%;
    text-shadow: 2px 2px 4px red;
    /* border: 1px solid orange; */
  }
  #llog{
    background: orange;
  }
  .con{
    width: 95%;
    height: 95%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    /* border: 1px solid black; */
  }
  .last{
    /* border: 1px solid pink; */
    width: 95%;
    margin-top: 40px;
    margin-left: 0px;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-content: center;
    height: 80px;
    margin-bottom: 20px;
  }
  p{
    text-align: center;
  }
  #ll{
    border: none;
  }
  .sp{
    font-size: 24px;
  }
} 


