@import url('https://fonts.googleapis.com/css2?family=Caramel&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Courgette&display=swap');
*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}
#log{
  background: rgb(234, 201, 33);
  color:white;
}
button{
  border: none;
  border-radius: 10px;
  outline: none;
  font-weight: 200;
  font-size: 22px;
}
#reg{
  background: rgb(75, 86, 247);
  color:white;
}
.Main_body{
  width: 100%;
  height: 100vh;
  /* border: 1px solid black; */
  /* overflow: hidden; */
  /* background:rgba(0,0,0,0.8); */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
h1{
  font-size: 70px;
  color:black;
  font-weight: 100;
  margin-top: 7%;
  font-family:  cursive;
}
h2{
  /* border: 1px solid black; */
  width: 440px;
  height: 300px;
  font-weight: 300;
  margin-top: 10px;
  font-size: 125px;
  text-align: center;
  font-family:  'Courgette',cursive;
}
._buttons{
  /* border: 2px solid red; */
  display: flex;
  padding: 5px;
  justify-content: center;
  width: 95%;
  height: auto;flex-direction: column;
  margin-top: 10%;
  align-items: center;
}
.buttons_l{
    width: 99%;
    height: 60px;
    margin-top: 10px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 20px;
    font-size: 22px;
    text-decoration: none;
    /* border: 1px solid black; */
  }
  a{
    text-decoration: none;
  }
@media (max-width:500px) {
  h1{
    font-size: 50px;
    color:black;
    font-weight: 100;
    margin-top: -60%;
    font-family:  cursive;
    margin-bottom: 50%;
  }
  h2{
    /* border: 1px solid black; */
    width: 300px;
    height: 300px;
    font-weight: 300;
    text-align: center;
    margin-top: -10%;
    font-size: 105px;
      font-family:  'Courgette',cursive;
  }
  ._buttons{
  /* margin-top: 10%; */
    /* margin-top:40%; */
    border: none;
  /* border: 1px solid black; */
  width: 95%;
  padding: 5px;
  margin-bottom: -70%;
     display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  height: auto;
  }
  .buttons_l{
    width: 99%;
    height: 50px;
    margin-top: 10px;
  }

} 
